import {createSlice} from "@reduxjs/toolkit";
import axios from "axios";

let count = 0;

const slice = createSlice({
    name: 'post',
    initialState: {
        namazTime: {},
        timings: [],
        getDayWow: {},
        weatherHourly: [],
        weatherDaily: [],
    },
    reducers: {
        getPost: (state, action) => {
            let thisMonth = new Date;
            state.namazTime = action.payload.weather.current
            state.weatherHourly = action.payload.weather.hourly
            state.weatherDaily = action.payload.weather.daily
            state.timings=[];
            count=0;
            action.payload.getApi.data.map(item => {
                if (parseInt(item.date.gregorian.day) === thisMonth.getUTCDate() && count < 10) {
                    count += 1
                    thisMonth.setUTCDate(thisMonth.getUTCDate() + 1)
                    state.timings.push(item)
                }
            })
            thisMonth = new Date();
            action.payload.getApi.data.filter((item) => {
                if (parseInt(item.date.gregorian.day) === thisMonth.getUTCDate() &&
                    parseInt(item.date.gregorian.month.number) === thisMonth.getUTCMonth()+1) {
                    console.log(thisMonth.getUTCMonth() + "  manashu")
                    console.log(parseInt(item.date.gregorian.month.number) + "   mammmmm")
                    state.getDayWow = item.timings
                    return item
                }
            })
        },
    }
})


export function getNamazTime(minute) {
    let minut = parseInt(minute)
    return function (dispatch) {
        axios({
            method: 'get',
            baseURL: 'http://213.183.45.68:8080/api/users/getByCity/' + minut
        }).then(res => {
            dispatch({
                type: 'post/getPost',
                payload: res.data
            })
        }).catch(err => {
            dispatch({
                type: 'post/getPost',
                payload: err.data
            })
        })
    }
}

export default slice.reducer